@import "../../global.scss";

.projects{
    background-color: $secondaryColor;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    h1{
        margin-top: 20px;
        font-size: 70px;
    }
    
    .cardSection{
        margin-top: 10px;
        width: 90%;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        overflow: hidden;
        justify-content: center;
        // border: 2px solid black;
        // height: 600px;
        
        .card{
            width: 370px;
            height: 270px;
            border-radius: 5px;
            border: 1px solid lightgray;
            margin: 15px 20px;
            display: flex;
            // align-items: center;
            // justify-content: center;
            color: white;
            overflow: hidden;
            
            a{
                display: flex;
                flex-direction: column;
                overflow: hidden;
                margin: 0;
                padding: 0;
                height: 100%;
                width: 100%;
                text-decoration: none;
                
                img{
                    margin: 0;
                    height: 80%;
                    width: 100%;
                    object-fit: cover;
                }

                .foot{
                    object-fit: cover;
                    text-align: center;
                    justify-content: center;
                    padding: 1rem 1rem;
                    background-color: white;
                    color: $primaryColor;
                    transition: all .4s ease;
                    
                    h3{   
                        font-size: 20px;
                    }

                }
                &:hover .foot{
                    background-color: $primaryColor;
                    color: $secondaryColor;
                }
    
            }

        }
    }
}