@import "../../global.scss";

.intro{
    display: flex;
    background-color: $secondaryColor;

    .left{
        // background-color: royalblue;
        flex: 0.5;
        overflow: hidden;
        
        .imgContainer{
            display: flex;
            align-items: center;
            justify-content: center;
            width: 700px;
            height: 700px;
            // background-color: crimson;

            img{
                height: 90%;
                transition: all 1s ease;

                &:hover{
                    height: 95%;
                }
            }
        }
    }
    .right{
        flex: 0.5;
        position: relative;
        // background-color: royalblue;

        .wrapper{
            height: 100%;
            width: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;

            h1{
                font-size: 60px;
                margin: 10px 0;
            }
            h2{
                font-size: 35px;
            }
            h3{
                font-size: 30px;

                span{
                    font-size: 30px;
                    color: crimson;
                }

                .ityped-cursor{
                    animation: blink 1s infinite;
                }

                @keyframes blink {
                    50%{
                        opacity: 1;
                    }
                    
                    100%{
                        opacity: 0;
                    }
                }
            }
        }
        a{
            position: absolute;
            bottom: 20px;
            // left: 40%;

            img{
                width: 30px;
                animation: arrowBlink 1s infinite;
            }

            @keyframes arrowBlink {
                100%{
                    opacity: 0;
                }
                
            }

        }
    }
}